
import axios from 'axios'
import CryptoJS from 'crypto-js'
import Box from 'vux/src/components/box/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Marquee from 'vux/src/components/marquee/marquee.vue'
import MarqueeItem from 'vux/src/components/marquee/marquee-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'


export default {
name: 'RefreshData',
directives: {
TransferDom
},
components: {
Box,
Confirm,
Countup,
Divider,
Marquee,
MarqueeItem,
TransferDom,
XButton,
XCircle,
XDialog
},
data () {
return {
totalStep: 4,
percent: 0,
step: 0,
marqueeMsg: null,
marqueeMsg0: [
'Tip:  40 ',
'',
'Tip:  seu-wlan ',
''
],
marqueeMsg1: [
'',
'Tip:  40 ',
'',
'Tip:  seu-wlan ',
'',
'Tip: ',
' GitHub',
'Tip: '
],
showHideOnBlur: true,
sessionID: '5CF1C1D07A81503A5AC5E62076C26136',
dataJson: {}
}
},
created: function () {
if (this.$cookies.get('xAuth') == null) {
this.$router.push({
path: '/login',
name: 'login'
})
} else {
this.marqueeMsg = this.marqueeMsg0
}
},
methods: {
stepPlus () {
this.step += 1
this.percent += (100 / this.totalStep)
this.marqueeMsg = this.marqueeMsg1
if (this.step === this.totalStep) {
this.percent = 200
this.marqueeMsg = this.marqueeMsg0
}
},
dataGzip () {
let pako = require('pako')
return pako.gzip(JSON.stringify(this.dataJson), { to: 'string' })
},
dataAES () {

const key = CryptoJS.enc.Utf8.parse('1234123412ABCDEF') 
const iv = CryptoJS.enc.Utf8.parse('ABCDEF1234123412') 
let encrypted = CryptoJS.AES.encrypt(this.dataGzip(), key,
{ iv: iv,
mode: CryptoJS.mode.CBC,
padding: CryptoJS.pad.Pkcs7 })
let dataAes = encrypted.ciphertext.toString()

return dataAes
},
async startSpider () {
this.getZccxCookie()
this.stepPlus()
let expiresMin = 10
let d = new Date()
this.dataJson = {}
d.setTime(d.getTime() + 60 * 1000 * expiresMin)
document.cookie =
'JSESSIONID=' + this.sessionID + ';path=/;expires=' + d.toGMTString()
await this.getHtmlAll()
this.dataAES()
},
async getHtmlAll () {
let page = this.totalStep - 2
do {
await this.getHtml(page)
} while (--page)
},
async getHtml (page) {
await this.getHtmlPromise(page)
.then(res => {
if (res.status !== '200') {
res = res.data
this.dataJson[page] = this.htmlSplit(res)


this.stepPlus()
} else {
}
})
.catch(function (e) {
console.log(e)
})
},
htmlSplit (res) {
var resRe =
'<td width=.+align="center">213(.+)</td>\\s+<td width=.+align="center">.+</td>\\s+<td width=.+align="center">.+</td>\\s+<td width=.+align="center">.+</td>\\s+<td width=.+align="center">.+</td>\\s+<td width=.+align="center">(.+)</td>'
var resReG = new RegExp(resRe, 'g')
var result = res.match(resReG)
let resultList = []
let len = result.length
let i = len - 1
if (i > -1) {
do {
let resultSplit = result[i].match(resRe)
resultList.push(resultSplit[1] + resultSplit[2])
} while (i-- > 1)
}

let resultStr = len + '.' + resultList.join('.')
return resultStr
},
getHtmlPromise (page) {
return new Promise((resolve, reject) => {
axios
.get(
'/SportWeb/gym/gymExercise/gymExercise_query_resultCnt.jsp?pageStr=' +
page
)
.then(function (response) {
resolve(response)
})
.catch(function (error) {
reject(error)
})
})
},
async getZccxCookie () {
let res = await axios.get('/basicTool/paocaoSpider')
console.log(res)
}
}
}



