
import axios from 'axios'
import Confirm from 'vux/src/components/confirm/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
name: 'Login',
directives: {
TransferDom
},
components: {
Confirm,
Loading,
Toast,
TransferDom
},
created: function () {
if (this.$cookies.get('__qc__k') == null) {

QC.Login.showPopup({
appId: '101529980',
redirectURI: 'https://seuxw.cn/webapp/paocao'
})
} else if (this.$cookies.get('xAuth') == null) {
this.loadShow = false
this.confirmShow = true
} else {

this.$router.push({
path: '/paocao',
name: 'paocao'
})
}
},
data () {
return {
loadShow: true,
toastShow: false,
toastShow2: false,
cardno: null,
confirmShow: false
}
},
methods: {
async getXAuth (tk, cardno) {
let i = 3

do {
var res = await axios.post('/xAuth?tk=' + this.$cookies.get('__qc__k') +
'&cardno=' + this.cardno)
res = res.data['data']['token']
} while (--i || res == null)
return { res: '555' }
},
async onConfirm (inputCardno) {
this.cardno = inputCardno
while (this.cardno < 213150000 || this.cardno > 213190000) {
this.toastShow2 = true
return
}
let xAuth = await this.getXAuth()

if (xAuth == null) {
this.toastShow = true
} else {
this.confirmShow = false
this.$cookies.set('xAuth', xAuth)
this.$router.push({
path: '/paocao',
name: 'paocao'
})
}
}
}
}



